@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.info-icon-container {
    flex-wrap: wrap;
}

.info-icon-text {
    flex-basis: 100%;
    font-size: small;
}

.avatar-link-container {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
}

.avatar-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.avatar-icon {
    margin-right: 0.5rem;
}

.avatar-nickname {
    font-size: small;
}

.video-container {
    position: relative;
    display: flex;
    max-height: 100%;
    max-width: 100%;
    background: black;
    //margin: auto;
}

.video-element-container {
    overflow: hidden;
    display: inline-flex;
    margin: 0 auto;
    max-height: 100%;
    max-width: 100%;
}

.video-button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: none;
}

.video-button.--display {
    display: block;
}

.post-container {
    display: flex;
    justify-content: space-between;
    margin: 5% auto;
    padding: 0.75rem;
    background: #cce5ff;
}

.post-info-container {
    display: flex;
    flex-direction: column;
    width: 50%;
    align-items: flex-start;
    background: white;
    box-sizing: border-box;
    padding: 0.5rem;
    margin-left: 0.75rem;
}

.post-text {
    padding: 0.5rem 0 0;
    text-align: start;
}

.post-reactions {
    display: flex;
    justify-content: space-around;
    align-self: center;
}

.post-divider {
    width: 100%;
    border: none;
    border-bottom: #cce5ff solid;
}

.post-hashtags {
    padding-top: 0.5rem;
    flex-grow: 1;
    text-align: start;
}

.profile-avatar {
    justify-content: center;
    padding: 1rem 0;
}

.profile-icon-group {
    margin: 1rem;
}

.user-video-container {
    position: relative;
}

.user-video-info {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 0.5rem;
    box-sizing: border-box;
    width: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.4);
    color: white;
    font-size: large;
}

.user-video-info span {
    margin-left: 0.25rem;
}

.video-grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 1rem;
    max-width: 100%;
    margin: 1rem 12%;
}

@media screen and (max-width: 1300px) {
    .video-grid {
        grid-template-columns: repeat(4, 1fr);
    }
}

@media screen and (max-width: 800px) {
    .video-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

.user-video-display {
    margin-left: auto;
    margin-right: auto;
    display: block;
}

.user-video-display video {
    width: 100%;
}

.App {
    text-align: center;
    margin-top: 64px;
}

